/**  Custom Formatter */
// format du client
function ClientFormatter(cell, row) {
    return (
        <span> {row.username} </span>
    )
}
// format du ticket
function TicketFormatter(cell, row) {

    return (
        <div> Here </div>
    )
}
// format de la date
function DateFormatter(cell, row) {
    const date = new Date(row.date)
    const created = date.toLocaleString()
    
    return(
        <span> {created} </span> 
    )
}

// format des options sur les données
function OptionFormatter(cell, row, rowIndex) {
    return (
        <div className="md-6 row ">
            <div className='pl-3 pr-3'>
                <Link to={`/show_machine/${row.key}`}><span className="fas fa-info-circle fa-1x text-info"/> </Link>
            </div>
            <div className='pl-3'>
                <Link to={`/edit_machine/${row.key}`}><span className="fas fa-pencil-alt fa-1x text-warning"/> </Link>
            </div>
        </div>
    )
}

// header style for datatable
const headingStyle = {
    backgroundColor : '#E5F1FB',
    color: ''
}

class ListTransaction extends Component {
    constructor(props, context) {
        super(props, context);
        this.state = { 
            activites: [],
            defaultActivites: [],
            path: [
                { 
                    label: 'Home',
                    value: '/dashboard_client',
                },
                { 
                    label: 'Clients',
                    value: '/list_transaction',
                },
                { 
                    label: 'Transactions',
                    value: '',
                }
            ],
            isLoading: true,
        };
        this._columns = [
            {
                dataField: 'key',
                text: 'ID',
                hidden: true
            },
            {
                dataField: 'client',
                text: 'Nom',
                headerStyle: headingStyle,
                formatter: ClientFormatter,
                sort: true
            },
            {
                dataField: 'wallet',
                text: 'Wallet',
                headerStyle: headingStyle,
                sort : true
            },
            {
                dataField: 'type',
                text: 'Type',
                headerStyle: headingStyle,  
                sort: true
            },
            {
                dataField: 'description',
                text: 'Description',
                headerStyle: headingStyle,
                sort: true
            },
            {
                dataField: 'ticket',
                text: 'Ticket',
                headerStyle: headingStyle,
                formatter: TicketFormatter,               
                sort: true
            },
            {
                dataField: 'date',
                text: 'Date',
                headerStyle: headingStyle,
                /*filter: dateFilter({
                    className: 'row',
                    getFilter(filter) {
                        listFilter.dateFilter = filter;
                    }
                }),*/
                formatter: DateFormatter,
                sort: true
            },
            {
                dataField:'option',
                text: 'Option',
                headerStyle: headingStyle,
                isDummyField: true,
                formatter: OptionFormatter
            }
        ];   
    }
    
    /* 1. connect directly to firestore /
    onCollectionUpdate = (querySnapshot) => {
        const clients = [];
        querySnapshot.forEach((doc) => {
          const { nom, zone, r_modele, level, localite, created, status } = doc.data();
          /* remplir le tableau /
          clients.push({
            key: doc.id,
            doc, // DocumentSnapshot
            nom,
            zone,
            localite,
            level,
            created,
            status,
          });
        });
        this.setState({
          defaultclients: clients,
          clients: clients, 
          isLoading: false,
       });
       console.log(this.state.defaultclients)
    }
    */
    
    componentDidMount() {
        axios.get(url_activites)
        .then((res) => {
            const activites = res.data.data
            console.log(activites)
            
            activites.map((activite) => {
                activite.description = this.checkDescription(activite)
            })

            this.setState({
                activites: activites    ,
                isLoading: false,
            })
        })
        .catch((error) => {
            console.log("get activites failed!", error)
        })
    }

    // description d'une transaction
    checkDescription = (activite) => {
        const type = activite.type
        
        switch(type) {
            case 'depot': {
                const machine = activite.machine
                const info = activite.info
                return <Depot machine={machine} info={info} />
            }
            case 'achat': {
                const categorie = activite.categorie
                const info = activite.info
                return <Achat categorie={categorie} info={info}/>
            }
        }
    }   

    render() {
        const options = {
            custom: true,
            paginationSize: 4,
            pageStartIndex: 1,
            sizePerPage: 4,
            firstPageText: 'First',
            prePageText: 'Back',
            nextPageText: 'Next',
            lastPageText: 'Last',
            nextPageTitle: 'First page',
            prePageTitle: 'Pre page',
            firstPageTitle: 'Next page',
            lastPageTitle: 'Last page',
            showTotal: true,
            paginationTotalRenderer: customTotal,
            sizePerPageList: [
            {
                text: '2', value: 2
            }, 
            {
                text: '5', value: 5
            }, 
            {
                text: 'All',
                value: this.state.activites.length
            }],
            totalSize: this.state.activites.length
        };
        const isLoading = this.state.isLoading;

        const contentTable = ({ paginationProps, paginationTableProps }) => (
            <div>
              <ToolkitProvider
                keyField="key"
                columns={ this._columns }
                data={ this.state.activites }
                search           
                >
                {
                  toolkitprops => (
                    <div>
                        {/*barre de recherche */}
                        <Row>                                       
                            {/*search bar */}
                            <div className="col-xl-4 align-self-center ml-auto">
                                <div className="media">
                                    <div className="media-body">
                                        <SearchBar 
                                            { ...toolkitprops.searchProps }
                                            className="custome-search-field"
                                            style={ { color: 'dark' } }
                                            placeholder="Rechercher un utilisateur, un type de transaction"                    
                                        />
                                    </div>
                                    <div className="align-self-start mr-2">
                                        <em className="fas fa-search fa-2x "></em>
                                    </div>
                                </div>
                            </div>
                        </Row>
                        {/*table */}
                        <Card className="m-20">
                            <CardBody className="ml-5 mr-5">
                                <BootstrapTable
                                bootstrap4
                                hover
                                //selectRow={selectRow}
                                { ...toolkitprops.baseProps }
                                { ...paginationTableProps }
                                />
                            </CardBody>
                            <CardFooter>
                                
                            </CardFooter>
                        </Card>
                        {/*Print or export */}
                        <div className="col">
                            <div className="media m-10">
                                {/*export icon */}
                                <ExportCSVButton { ...toolkitprops.csvProps } className="d-flex p-0 mr-5" >
                                    <Button color="secondary">
                                        <em className="fas fa-share text-info"></em>
                                    </Button>                          
                                    <em className="align-self-center text-dark text-bold">  Exporter au format CSV </em>
                                </ExportCSVButton>
                                {/*print icon */}
                                <Link to="" title="Imprimer la liste" className="d-flex mr-3">
                                    <Button color="secondary">
                                        <em className="fa fa-print text-purple "></em>
                                    </Button>                                
                                    <em className="align-self-center text-dark text-bold">  Imprimer la liste </em>
                                </Link>
                                <div className="col-xl-3">
                                    .
                                </div>
                                {/*updated date */}
                                <div className="col-xl-1 ml-auto p-0">
                                    <small className="mb-0 mt-1 text-dark">Mis à jour le : 04/05/21 14:24:18</small>
                                </div>
                            </div>
                        </div>                        
                    </div>
                  )
                }
              </ToolkitProvider>
                    {/*pagination list */}
                        <div className="ml-auto col-xl-3">
                            <PaginationListStandalone { ...paginationProps } />
                        </div>
            </div>
        );
        
        const customTotal = (from, to, size) => (
            <span className="react-bootstrap-table-pagination-total">
              Showing { from } to { to } of { size } Results
            </span>
        );
        
        return  (
            isLoading? (
                <PageLoader/>
            ):(
                <ContentWrapper>
                    <PaginationProvider pagination={paginationFactory(options)}>
                        { contentTable }
                    </PaginationProvider>
                </ContentWrapper>
            )
        )
    }
}

export default ListTransaction;